<template>
  <div>
    <h2 class="title">
      {{$route.name == '修改菜单' ? '修改菜单栏配置' : '配置新菜单栏'}}
    </h2>
    <div class="edit-info">
      <Form :model="formData" class="form-group" label-position="right" :label-width="100">
        <FormItem label="选择公号" prop="codes">
          <CheckboxGroup v-model="codes">
            <Checkbox v-for="(item, index) in gonghaoList" :label="item.code" name="codes">{{item.name}}</Checkbox>
          </CheckboxGroup>
        </FormItem>
        <FormItem label="标题" prop="title">
          <Input class="inputtxt" size="large" type="text" v-model.trim="title" placeholder="请输入标题"></Input>
        </FormItem>
        <FormItem label="是否开启个性化菜单" prop="individual">
          <i-switch v-model="individual"/>
        </FormItem>
        <FormItem label="链接/关键词" v-if="individual">
          <RadioGroup v-model="sex">
            <Radio label="0">未知</Radio>
            <Radio label="1">男</Radio>
            <Radio label="2">女</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="二级菜单" prop="has_sub_btn">
          <i-switch v-model="has_sub_btn"/>
        </FormItem>
        <FormItem label="一级菜单名称" prop="name">
          <Input class="inputtxt" size="large" type="text" v-model.trim="formData.name" placeholder="请输入一级菜单名称"></Input>
        </FormItem>
        <!-- 没有二级菜单 -->
        <FormItem label="链接/关键词" v-show="!has_sub_btn">
          <RadioGroup v-model="formData.type">
            <Radio label="view">view</Radio>
            <Radio label="click">click</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="一级菜单链接" prop="url" v-show="!has_sub_btn && formData.type == 'view'">
          <Input class="inputtxt" size="large" type="text" v-model.trim="formData.url" placeholder="请输入一级菜单链接"></Input>
        </FormItem>
        <FormItem label="一级菜单关键字" prop="key" v-show="!has_sub_btn && formData.type == 'click'">
          <Input class="inputtxt" size="large" type="text" v-model.trim="formData.key" placeholder="请输入一级菜单关键字"></Input>
        </FormItem>
        <!-- 没有二级菜单   end    -->
        <!-- 有二级菜单 -->
        <Form :model="subItem" class="form-group" label-position="right" :label-width="100">
          <FormItem label="二级菜单名称" prop="name" v-show="has_sub_btn">
            <Input class="inputtxt" size="large" type="text" v-model.trim="subItem.name"
                   placeholder="请输入二级菜单名称"></Input>
          </FormItem>
          <FormItem label="链接/关键词" v-show="has_sub_btn">
            <RadioGroup v-model="subItem.type">
              <Radio label="view">view</Radio>
              <Radio label="click">click</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="二级菜单链接" prop="url" v-show="has_sub_btn && subItem.type == 'view'">
            <Input class="inputtxt" size="large" type="text" v-model.trim="subItem.url" placeholder="请输入二级菜单链接"></Input>
          </FormItem>
          <FormItem label="二级菜单关键词" prop="key" v-show="has_sub_btn && subItem.type == 'click'">
            <Input class="inputtxt" size="large" type="text" v-model.trim="subItem.key"
                   placeholder="请输入二级菜单关键词"></Input>
          </FormItem>
        </Form>
        <Button size="large" type="primary" v-show="has_sub_btn" @click="addSubBtn()">添加二级菜单</Button>
        <Button size="large" type="success" v-show="secSave" @click="saveSecond()" style="margin-left: 30px;">保存
        </Button>
        <Table v-if="secData.length != 0" class="second-table" stripe :columns="secHeader" :data="secData"></Table>
        <span class="line" v-show="has_sub_btn"></span>
        <Button size="large" type="primary" @click="addBtn()">添加一级菜单</Button>
        <Button size="large" type="success" @click="saveFirst()" v-show="firstSave" style="margin-left: 30px;">保存
        </Button>
        <Table class="first-table" stripe :columns="firstHeader" :data="firstData"></Table>
        <Button class="btn" size="large" type="primary" @click="submitForm">提交</Button>
        <Button size="large" type="error" @click="cancel">取消</Button>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        codes: [],
        formData: {
          sub_button: [],
          type: "view"
        },
        title: "",
        individual: false,
        sex: "0",
        firstData: [],
        has_sub_btn: false,
        subItem: {
          type: "view"
        },
        radio: "url",
        firstSave: false,
        secSave: false,
        index: null,
        index1: null,
        secData: [],
        firstHeader: [
          {
            key: "name",
            title: "一级菜单名称",
            align: "center",
            render: (h, params) => {
              let name = params.row.name ? params.row.name : "~";
              return h(
                "span",
                {
                  props: {}
                },
                name
              );
            }
          },
          {
            title: "Action",
            align: "center",
            render: (h, params) => {
              let that = this;
              return h("div", [
                h(
                  "Button",
                  {
                    style: {
                      marginRight: "10px"
                    },
                    props: {
                      type: "primary",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.firstSave = true;
                        that.formData = params.row;
                        that.index = params.index;
                        if (that.formData.sub_button) {
                          that.secData = that.formData.sub_button;
                          that.has_sub_btn = true;
                          that.subItem = that.secData[0];
                          that.secSave = true;
                        }
                      }
                    }
                  },
                  "编辑"
                ),
                h(
                  "Button",
                  {
                    props: {
                      type: "error",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.firstData.splice(params.index, 1);
                        that.formData = {
                          sub_button: [],
                          type: "view"
                        };
                        that.subItem = {};
                        that.has_sub_btn = false;
                        that.secData = [];
                      }
                    }
                  },
                  "删除"
                )
              ]);
            }
          }
        ],
        secHeader: [
          {
            key: "name",
            title: "二级菜单名称",
            align: "center"
          },
          {
            title: "Action",
            align: "center",
            render: (h, params) => {
              let that = this;
              return h("div", [
                h(
                  "Button",
                  {
                    style: {
                      marginRight: "10px"
                    },
                    props: {
                      type: "primary",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.index1 = params.index;
                        that.subItem = params.row;
                        that.secSave = true;
                      }
                    }
                  },
                  "编辑"
                ),
                h(
                  "Button",
                  {
                    props: {
                      type: "error",
                      size: "small"
                    },
                    on: {
                      click() {
                        that.formData.sub_button.splice(params.index, 1);
                        that.subItem = {
                          type: "view"
                        };
                        that.has_sub_btn = true;
                      }
                    }
                  },
                  "删除"
                )
              ]);
            }
          }
        ],
        gonghaoList: []
      };
    },
    mounted() {
      this.showGonghaoList();
      if (this.$route.name === "修改菜单") {
        this.firstSave = true;
        this.data_info = this.$route.params;
        this.codes = this.data_info.codes;
        this.title = this.data_info.title;
        this.individual = this.data_info.individual;
        this.sex = this.data_info.sex;
        this.firstData = this.data_info.values;
        this.formData = this.firstData[0];
        if (this.formData.sub_button) {
          this.secData = this.formData.sub_button;
          this.has_sub_btn = true;
          this.secSave = true;
          this.subItem = this.secData[0];
        }
      }
    },
    methods: {
      showGonghaoList() {
        this.$axios.get("/conf").then(res => {
          this.gonghaoList = res.data.data;
        });
      },
      cancel() {
        this.codes = [];
        this.individual = false;
        this.sex = "0";
        this.title = "";
        this.resetForm();
        this.$router.push({name: "配置菜单"});
      },
      resetForm() {
        this.formData = {
          sub_button: [],
          type: "view"
        };
        this.subItem = {
          type: "view"
        };
      },
      addBtn() {
        this.formData.sub_button = this.secData;
        this.secData = [];
        if (this.formData.sub_button.length == 0) {
          delete this.formData.sub_button;
        }
        this.firstData.push(this.formData);
        this.resetForm();
        this.has_sub_btn = false;
      },
      submitForm() {
        if (this.firstData.length != 0) {
          if (this.$route.name === "新建菜单") {
            this.$axios
              .post("/menu/create", {
                codes: this.codes,
                title: this.title,
                values: this.firstData,
                individual: this.individual,
                sex: this.sex
              })
              .then(res => {
                this.firstData = [];
                this.$Message.info("创建成功");
                this.cancel();
              });
          } else if (this.$route.name === "修改菜单") {
            this.$axios
              .post("/menu/update", {
                id: this.data_info._id,
                codes: this.codes,
                title: this.title,
                values: this.firstData,
                individual: this.individual,
                sex: this.sex
              })
              .then(res => {
                this.firstData = [];
                this.$Message.info("修改成功");
                this.cancel();
              });
          }
        } else {
          this.$Message.info("菜单列表不能为空");
        }
      },
      addSubBtn() {
        this.secData.push(this.subItem);
        this.subItem = {
          type: "view"
        };
        this.has_sub_btn = true;
      },
      saveFirst() {
        this.firstData.splice(this.index, 1, this.formData);
        this.secData = [];
        this.resetForm();
        this.firstSave = false;
      },
      saveSecond() {
        this.secData.splice(this.index1, 1, this.subItem);
        this.has_sub_btn = true;
        this.secSave = false;
        this.subItem = {
          type: "view"
        };
      }
    }
  };
</script>

<style scoped>
  .form-group {
    width: 800px;
    text-align: left;
  }

  .edit-info {
    width: 800px;
    margin: 0 auto;
    text-align: left;
  }

  .title {
    font-size: 30px;
    margin-bottom: 30px;
  }

  .inputtxt {
    width: 300px;
  }

  .second-table {
    margin: 30px;
  }

  .line {
    height: 30px;
    margin: 30px 40px 0;
    display: block;
    border-top: 1px dashed #000;
  }

  .first-table {
    margin: 30px 0;
  }

  .btn {
    margin-right: 20px;
  }
</style>
